<template>
  <section class="search-box">
    <div class="card" v-for="book in searchArr" :key="book.volumeInfo.title">
      <div class="search-img">
        <img :src="book.volumeInfo.imageLinks.thumbnail" alt="" />
      </div>
      <div class="search-info">
        <h3 class="search-title">{{ book.volumeInfo.title }}</h3>
        <p class="search-desc">
          {{ book.volumeInfo.description }}
        </p>
        <a :href="book.volumeInfo.canonicalVolumeLink" target="_blank"
          >Read More</a
        >
      </div>
    </div>
  </section>
</template>

<script setup>
import { useRoute } from "vue-router";
import { watch, ref } from "vue";

const route = useRoute();
const query = ref(route.query.result);

const booksURL = "https://www.googleapis.com/books/v1/volumes?q=";
let searchArr = ref([]);
const getBooks = (data) => {
  console.log(data);
  for (let i = 0; i < data.length; i++) {
    if (
      data[i].volumeInfo.title &&
      data[i].volumeInfo.description &&
      data[i].volumeInfo.imageLinks?.thumbnail
    ) {
      searchArr.value.push(data[i]);
    }
  }
  console.log(searchArr.value);
  searchArr.value.forEach((book) => {
    if (book.volumeInfo.description.length > 200) {
      book.volumeInfo.description =
        book.volumeInfo.description.slice(0, 200) + "...";
    }
  });
};
fetch(
  booksURL +
    query.value +
    "&startIndex=0&maxResults=40&printType=books&orderBy=relevance"
)
  .then((response) => response.json())
  .then((data) => getBooks(data.items));

watch(route, (to) => {
  query.value = to.query.result;
  searchArr.value = [];

  fetch(
    booksURL +
      query.value +
      "&startIndex=0&maxResults=40&printType=books&orderBy=relevance"
  )
    .then((response) => response.json())
    .then((data) => getBooks(data.items));
});
</script>

<style scoped>
.card {
  border: 1px solid rgb(228, 207, 207);
  border-radius: 20px;
  padding: 2rem 3rem;
  margin: 0 1rem;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  margin-bottom: 2rem;
}
.search-info {
  margin-left: 1rem;
}
.search-desc span {
  margin-left: 0.2rem;
}
</style>
