<template>
  <div class="top-picks">
    <h1>Our TOP 10 Picks This Month</h1>
    <p>swiper here</p>
  </div>
</template>

<script setup>
import { ref } from "vue";

const booksURL = "https://www.googleapis.com/books/v1/volumes?q=";
let bookArr = ref([]);

fetch(
  booksURL +
    "finance" +
    "&startIndex=0&maxResults=10&printType=books&orderBy=relevance"
)
  .then((response) => response.json())
  .then((data) => getItems(data.items));

const getItems = (data) => {
  console.log(data);
  bookArr.value = data;
  bookArr.value.forEach((book) => {
    if (book.volumeInfo.title.length > 25) {
      book.volumeInfo.title = book.volumeInfo.title.slice(0, 25) + "...";
    }
  });
};
</script>

